
import { Vue, Component, Prop } from "vue-property-decorator"

@Component({ name: "ModalRegistrarTutoria" })
export default class ModalRegistrarTutoria extends Vue {
  dialog = false

  @Prop({ required: true })
  nome!: string

  @Prop({ required: true })
  data!: string

  @Prop({ required: true })
  horario!: string

  registrarTutoria() {
    this.$emit("input", false)
  }
}
